import React from 'react'

// UTILS
import {
  BrowserRouter as Router,
  Route,
  Switch,
  HashRouter,
} from 'react-router-dom'
import TransitionGroup from 'react-transition-group/TransitionGroup' // ES6
import {get} from "lodash";

// COMPONENTS
import Animator from './components/ui/animator/Animator';
import Header from "./components/header/Header";
import EventsList from './pages/eventsList/EventsList';
import ContentDetails from './pages/contentDetails/ContentDetails';

class AppRouter extends React.Component {
  constructor(props) {
    super(props);
    this.lastScroll = 0
    this.pages = [
      {
        path: '/',
        exact: true,
        title: 'Homepage',
        component: EventsList
      },
      {
        path: '/search/:query',
        exact: false,
        title: 'Search',
        component: EventsList
      },
      {
        path: '/content/:id',
        exact: false,
        title: 'Event',
        component: ContentDetails
      }
    ]
    this.state = {
      scrollDirection: 'none',
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll)
  }

  onScroll = () => {
    if (
      this.lastScroll < window.pageYOffset &&
      this.state.scrollDirection !== 'down'
    ) {
      this.setState({
        scrollDirection: 'down'
      })
    } else if (
      this.lastScroll > window.pageYOffset &&
      this.state.scrollDirection !== 'up'
    ) {
      this.setState({
        scrollDirection: 'up'
      })
    }
    this.lastScroll = window.pageYOffset
  }


  render() {
    return (
      <HashRouter>
        <Route render={({location}) => (
          <div className={`scrollPage__${this.state.scrollDirection}`}>
            <Header/>
            <div className="header__below">
              <TransitionGroup>
                <Animator key={location.pathname} loc={location.pathname}>
                  <Switch>
                    {this.pages.map((page, index) => {
                      return (
                        <Route
                          key={index}
                          path={page.path}
                          exact={page.exact}
                          render={(match) => (
                            <page.component
                              match={match.match}
                              title={page.title}
                            />
                          )}/>
                      )
                    })
                    }
                    <Route component={Page404}/>
                  </Switch>
                </Animator>
              </TransitionGroup>
            </div>
          </div>
        )}/>
      </HashRouter>
    )
  }
}

const Page404 = () => {
  return (

    <div className='mainContainer'>
      <h2 className='text-center'>Not Found...</h2>
    </div>
  )
}


export default AppRouter





