import React from 'react'
import { connect } from 'react-redux';

// UTILS
import {get} from "lodash";

// COMPONENTS
import Event from "../../components/event/Event";
import Suggestions from "../../components/suggestions/Suggestions";
require('./style.scss')

class ContentDetails extends React.Component {
	constructor(props) {
		super(props)
	}
	
	componentDidMount() {
    this.root = this.refs.container
  }
	
	render() {
    const id = get(this.props, 'match.params.id')
    const event = get(this.props, `eventList.events.${id}`, {})
    return (
      <div className="container">
				<div className="eventPage">
					<div className="eventPage__left">
            <Event
              event={event}
            />
					</div>
          <div className="eventPage__right">
						<div className="eventPage__right-section">
              <h2 className='eventPage__right-title'>Dove mangiare qua intorno...</h2>
							<Suggestions/>
						</div>
					</div>
				</div>
      </div>
		)
	}
}

ContentDetails.propTypes={

}


function mapStateToProps(state) {
  return {
    eventList: state.rootReducer.eventList
  }
}

export default connect(mapStateToProps)(ContentDetails)


