import React from 'react'
import PropTypes from 'prop-types'
import {get} from 'lodash';

require('./style.scss')

export default class Event extends React.Component {
	constructor(props) {
		super(props)
	}
	render() {
		const title = get(this.props.event, 'title')
    const description = get(this.props.event, 'description')
    const width = get(this.props.event, 'imageSize.width')
    const height = get(this.props.event, 'imageSize.height')
		const imageSrc = `http://via.placeholder.com/${width}x${height}`
		return (
			<div className="event">
				<div className="event__caption">
          <h1 className="event__title">{title}</h1>
				</div>
        <img className="event__cover" src={imageSrc}/>
			</div>
		)
	}
}

Event.propTypes={
  event: PropTypes.object,
}


