import {cloneDeep, slice} from 'lodash';

const INIT = 'eventList/INIT'
const SET = 'eventList/SET'

const fakeText = "Assertively transition transparent growth strategies rather than stand-alone convergence. Appropriately whiteboard integrated partnerships vis-a-vis market-driven platforms. Efficiently harness prospective niche markets for covalent alignments. Monotonectally aggregate team driven outsourcing with intuitive process improvements. Phosfluorescently communicate leading-edge services after competitive materials."
const fakeTextAr = fakeText.split(" ");
const events = {}
for (let a = 0; a < 12; a++) {
	const initAr = Math.floor(Math.random()*8)
  events[a] = {
    title: slice(fakeTextAr, initAr, (initAr + 2) + Math.floor(Math.random()*6)).join(" "),
		id: a,
    imageSize: {
      width: 300,
      height: 150 + Math.floor(Math.random()*100)
    },
    description: 'Authoritatively whiteboard multimedia based relationships whereas unique experiences. Dynamically evolve unique e-commerce for sticky intellectual capital. Efficiently fashion accurate initiatives without front-end ROI. Rapidiously brand business manufacture'
  }
}

const initialState = {
  events: events
}


export function init() {
	return {
		type: INIT
	}
}



export default function eventList(state = cloneDeep(initialState), action) {
	switch (action.type) {
		case INIT:
			return {}
		case SET:
		  let newState = cloneDeep(state)
     //
			return newState
		default:
			return state;
	}
}


