import React from 'react'
import Suggestion from "./Suggestion";

require('./style.scss')

export default class Suggestions extends React.Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div className="suggestions">
          <Suggestion/>
          <Suggestion/>
          <Suggestion/>
      </div>
    )
  }
}

Suggestions.propTypes = {}


